export default {
	data () {
		return {
			userInfo: {},
			platform: 'ios'
		}
	},
	onShow () {
		this.getUserInfo();
	},
	methods: {
		// 获取用户信息
		getUserInfo () {
			let _this = this;
			uni.getSystemInfo({
				success: function (res) {
					_this.platform = res.platform;
				}
			});
			
			this.$api.userInfo({}, res => {
				if (res.status) {
					this.userInfo = res.data
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		},
		// 页面跳转
		navigateToHandle (pageUrl) {
			this.$common.navigateTo(pageUrl)
		}
	}
}	
